import {Admin_estates_model} from '../model/model.js';
import {Admin_estates_view} from '../view/view.js';


export class Admin_estates_controller{

	constructor(){

		this.model = new Admin_estates_model();
		this.view = new Admin_estates_view();


		this.view.articles_event_listener((index)=>{

			if(index === 1){
				this.model.fetch_estates();

				this.model.registerListener((state)=>{

					this.view.render_estates(state);

					this.view.get_selected_estate(
						this.get_selected_estate
					);

					this.view.edit_selected_estate(
						this.edit_selected_estate
					);
				});
			}
			else if(index === 2){

				this.model.registerListener((state)=>{
					this.view.render_add_estate(state)
				})

				this.view.add_estate(
					this.add_estate
				)
			}
			else if(index === 3){

				this.model.registerListener((state)=>{
					this.view.render_delete_estate(state)
				})
				this.model.fetch_estates();
				this.view.delete_estate(
					this.delete_estate
				)
			}
		})
	}

	get_selected_estate = (selected_name) => {
		
		this.model.get_selected_estate(selected_name)
	}

	edit_selected_estate = (data_object) => {
		this.model.edit_selected_estate(data_object)
	}


	add_estate = (data_object) => {
		this.model.add_new_estate(data_object);
	}

	delete_estate = (name) => {
		this.model.delete_estate(name)
	}
}

