export class Admin_estates_view{

	constructor(){

		this.clicked_btn = false;
		this.get_selected_estate_function = null;
		this.selected_img = null;
		this.edit_selected_estatefunction = null;
		this.selected_delete_ul = null;

	}

	articles_event_listener = (controllerFunction) => {

		const btns = Array.from(
			document.getElementsByClassName("admin_estates_page_main_section_article_div_span")
		)

		let current_btn = null;

		    btns.forEach((btn, index) => {

		      btn.addEventListener("click", () => {

		      	current_btn = `admin_estates_page_main_section_article_div_span${index + 1}`;

		        const btn_div = document.getElementById(
		          `admin_estates_page_main_section_section1_article_div${index + 1}`
		        );

		        if (this.clicked_btn) {
		          this.clicked_btn = false;
		          btn_div.style.display = "none";
		          btn.textContent = "Click Here";
		          btn.style.fontSize = "110%";
		          btn.style.color = "var(--fourth-color)";
		          btn.style.backgroundColor = "var(--third-color)";
		          btn.style.border = "var(--fourth-color) solid 2px";

		        } else {
		          this.clicked_btn = true;
		          btn_div.style.display = "flex";
		          btn.textContent = "X";
		          btn.style.fontSize = "200%";
		          btn.style.color = "darkred";
		          btn.style.border = "red solid 2px";
		          controllerFunction(index+1)
		        }

		        const other_divs = Array.from(
		        	document.getElementsByClassName(`admin_estates_page_main_section_article_div1`)
		        )

		        other_divs.forEach((div,index2)=>{

		        	if(div.id !== btn_div.id ){

		        		div.style.display = 'none';

		        	}
		        })

	    		const reset_btns = Array.from(
					document.getElementsByClassName("admin_estates_page_main_section_article_div_span")
				)

				reset_btns.forEach((btn2)=>{
					if(current_btn !== btn2.id){
			          btn2.textContent = "Click Here";
			          btn2.style.fontSize = "110%";
			          btn2.style.color = "var(--fourth-color)";
			          btn2.style.backgroundColor = "var(--third-color)";
			          btn2.style.border = "var(--fourth-color) solid 2px";
					}
				})
	    

		    });

		})
	}

	render_estates = (state) => {

	    const loading = document.getElementById("admin_estates_page_main_section_section1_article_div1_loading");
	    const no_estates = document.getElementById("admin_estates_page_main_section_section1_article_div1_h3");
	    const ul_estate_titles = document.getElementById("admin_estates_page_main_section_section1_article_div1_ul2");
		const loading2 = document.getElementById("admin_estates_page_main_section_section1_article_div2_loading");

		const estate_info = document.getElementById("admin_estates_page_main_section_section1_article1_div3");
		const estate_title = document.getElementById("admin_estates_page_main_section_section1_article1_div3_ul1_li1_p2");
	    const estate_address = document.getElementById("admin_estates_page_main_section_section1_article1_div3_ul1_li1_p1");
	    const estate_text = document.getElementById("admin_estates_page_main_section_section1_article1_div3_div1_p1");
	    const estate_google_street = document.getElementById("admin_estates_page_main_section_section1_article1_div3_ul2_li1_p2");
	    const estate_google_maps = document.getElementById("admin_estates_page_main_section_section1_article1_div3_ul2_li1_p1");
	    const estate_images = document.getElementById("admin_estates_page_main_section_section1_article1_div3_ul3");

		const estate_form = document.getElementById("edit_estates_form");

		const loading3 = document.getElementById("edit_estates_form_div_loading");
		const err_msg = document.getElementById("edit_estates_form_err_msg");
		const success_msg = document.getElementById("edit_estates_form_success_msg");

	    // Helper to hide everything
	    const hideAll = () => {
	        loading.style.display = 'none';
	        no_estates.style.display = 'none';
	        loading2.style.display = 'none';
	        estate_info.style.display = 'none';
	        estate_form.style.display = 'none';
	        loading3.style.display = 'none';
	        err_msg.style.display = 'none';
	        success_msg.style.display = 'none';

	        estate_title.textContent = "";
	        estate_address.textContent = "";
	        estate_text.textContent = "";
	        estate_google_street.textContent = "";
	        estate_google_maps.textContent = "";
	    }

	    // 1️⃣ Loading
	    if (state.loading) {

	        hideAll();

	        loading.style.display = 'block';
	        loading2.style.display = 'block';
	        loading3.style.display = 'block';
	    }
	    // 2️⃣ estates loaded successfully
	    if (state.estates.view.estates_array) {

	        hideAll();

	    	ul_estate_titles.replaceChildren();
			estate_images.replaceChildren();

	        state.estates.view.estates_array.forEach((estate)=>{

	        	const li = document.createElement("li");

	        	li.classList.add("admin_estates_page_main_section_section1_article_div1_ul2_li")
	        	li.setAttribute("name", estate.estate_title)
	        	li.textContent = estate.estate_title
				
				ul_estate_titles.appendChild(li)

				li.addEventListener("click", (event)=>{

					this.get_selected_estate_function(
						event.currentTarget.getAttribute("name")
					);

					const other_lis = Array.from(
						document.getElementsByClassName("admin_estates_page_main_section_section1_article_div1_ul2_li")
					)

					other_lis.forEach((other_li)=>{
						other_li.classList.remove("admin_estates_page_main_section_section1_article_div1_ul2_li_selected")
					})

					event.currentTarget.classList.add("admin_estates_page_main_section_section1_article_div1_ul2_li_selected")
						
				})
	        })

	    }
	    // 3️⃣ no estates to load
	    if (!state.estates.view.estates_array) {

	        hideAll();
	       	ul_estate_titles.replaceChildren();
			estate_images.replaceChildren();
	        
	        no_estates.style.display = 'block';

	    }
	    // 4️⃣ estates loaded successully & selected estate title
	     if (state.estates.view.selected_estate && 
	    	state.estates.view.estates_array) {

	        hideAll();

			estate_images.replaceChildren();

			estate_info.style.display = 'flex';
			estate_form.style.display = 'flex';

			console.log(state.estates.view.selected_estate.elements[0])

	       	estate_title.textContent = state.estates.view.selected_estate.elements[0].estate_title;
	        estate_address.textContent = state.estates.view.selected_estate.elements[0].estate_address;
	        estate_text.textContent = state.estates.view.selected_estate.elements[0].estate_text;
	        estate_google_street.textContent = state.estates.view.selected_estate.elements[0].estate_google_street_url;
	        estate_google_maps.textContent = state.estates.view.selected_estate.elements[0].estate_google_maps_url;


	        state.estates.view.selected_estate.images.forEach((estate_img)=>{

	        	const li = document.createElement("li");
	        	const img = document.createElement("img");

	        	li.classList.add("admin_estates_page_main_section_section1_article1_div3_ul3_li");
	        	img.classList.add("admin_estates_page_main_section_section1_article1_div3_ul3_li_img");
	        	img.setAttribute("name", estate_img.estate_image_id)

	        	if(process.env.NODE_ENV === "production"){
	        		img.setAttribute("src", estate_img.estate_image_production_url)
	        	}
	        	else{
	        		img.setAttribute("src", estate_img.estate_image_development_url)
	        	}	

	        	img.addEventListener("click", (event)=>{

	        		this.selected_img = event.currentTarget.getAttribute("name");

					const other_imgs = Array.from(
						document.getElementsByClassName("admin_estates_page_main_section_section1_article1_div3_ul3_li_img")
					)

					other_imgs.forEach((other_img)=>{
						other_img.classList.remove("admin_estates_page_main_section_section1_article1_div3_ul3_li_img_selected")
					})

					event.currentTarget.classList.add("admin_estates_page_main_section_section1_article1_div3_ul3_li_img_selected")
	        	})

	        	li.appendChild(img);
	        	estate_images.appendChild(li)

	        })

	        estate_form.onsubmit = (e) =>{

				e.preventDefault();

				console.log("form clicked")


				const estate_address = document.getElementById("edit_estates_form_address").value;
				const estate_google_maps = document.getElementById("edit_estates_form_google_maps").value;
				const estate_google_street = document.getElementById("edit_estates_form_google_street").value;
				const estate_text = document.getElementById("edit_estates_form_text").value;
				const estates_new_image = document.getElementById("edit_estates_form_images").files[0];

				const result = {};

				if(this.selected_img){

					result.image = estates_new_image;
					result.image_name = this.selected_img
				}

				if(estate_address.length > 0){
					result.estate_address = estate_address
				}
				if(estate_google_maps.length > 0){
					result.estate_google_maps = estate_google_maps
				}
				if(estate_google_street.length > 0){
					result.estate_google_street = estate_google_street
				}
				if(estate_text.length > 0){
					result.estate_text = estate_text
				}

				result.category = state.estates.view.selected_estate.estate_title;

				this.edit_selected_estatefunction(result)
			}
		}
	    // 5 estates loaded successully & successful form submit
	    if (state.estates.edit.added_new_estate){
	

 			hideAll();

			estate_images.replaceChildren();

			estate_info.style.display = 'flex';
			estate_form.style.display = 'flex';

	       	estate_title.textContent = state.estates.view.selected_estate.elements[0].estate_title;
	        estate_address.textContent = state.estates.view.selected_estate.elements[0].estate_address;
	        estate_text.textContent = state.estates.view.selected_estate.elements[0].estate_text;
	        estate_google_street.textContent = state.estates.view.selected_estate.elements[0].estate_google_street_url;
	        estate_google_maps.textContent = state.estates.view.selected_estate.elements[0].estate_google_maps_url;

	        console.log(state.estates.view.selected_estate.images)
	        state.estates.view.selected_estate.images.forEach((estate_img)=>{

	        	const li = document.createElement("li");
	        	const img = document.createElement("img");

	        	li.classList.add("admin_estates_page_main_section_section1_article1_div3_ul3_li");
	        	img.classList.add("admin_estates_page_main_section_section1_article1_div3_ul3_li_img");
	        	img.setAttribute("name", estate_img.estate_image_id)

	        	if(process.env.NODE_ENV === "production"){
	        		img.setAttribute("src", estate_img.estate_image_production_url)
	        	}
	        	else{
	        		img.setAttribute("src", estate_img.estate_image_development_url)
	        	}	

	        	img.addEventListener("click", (event)=>{

	        		this.selected_img = event.currentTarget.getAttribute("name");

					const other_imgs = Array.from(
						document.getElementsByClassName("admin_estates_page_main_section_section1_article1_div3_ul3_li_img")
					)

					other_imgs.forEach((other_img)=>{
						other_img.classList.remove("admin_estates_page_main_section_section1_article1_div3_ul3_li_img_selected")
					})

					event.currentTarget.classList.add("admin_estates_page_main_section_section1_article1_div3_ul3_li_img_selected")
	        	})

	        	li.appendChild(img);
	        	estate_images.appendChild(li)

	        })

	        estate_form.onsubmit = (e) =>{

				e.preventDefault();

				console.log("form clicked")


				const estate_address = document.getElementById("edit_estates_form_address").value;
				const estate_google_maps = document.getElementById("edit_estates_form_google_maps").value;
				const estates_google_street = document.getElementById("edit_estates_form_google_street").value;
				const estates_text = document.getElementById("edit_estates_form_text").value;
				const estates_new_image = document.getElementById("edit_estates_form_images").files[0];

				const result = {};

				if(this.selected_img){

					result.image = estates_new_image;
					result.image_name = this.selected_img
				}

				if(estate_address.length > 0){
					result.estate_address = estate_address
				}
				if(estate_google_maps.length > 0){
					result.estate_google_maps = estate_google_maps
				}
				if(estates_google_street.length > 0){
					result.estates_google_street = estates_google_street
				}
				if(estates_text.length > 0){
					result.estates_text = estates_text
				}


				result.category = state.estates.view.selected_estate.estate_title

				this.edit_selected_estatefunction(result)
			}
			estate_form.reset();
			success_msg.style.display = 'flex';
			err_msg.style.display = 'none';
	        
	    }
	    // 6 estates loaded successully & ERROR form submit
	    if (state.estates.edit.attempted_added_new_estate) {

			success_msg.style.display = 'none';
	       	err_msg.style.display = 'flex';
	    }

	    
	}

	get_selected_estate = (modelFunction) => {
		this.get_selected_estate_function = modelFunction;
	}

	edit_selected_estate = (modelFunction) => {
		this.edit_selected_estatefunction = modelFunction
	}

	render_add_estate = (state) => {

		const loading = document.getElementById("add_estates_form_div_loading");
		const err = document.getElementById("add_estates_form_err_msg");
		const success = document.getElementById("add_estates_form_success_msg");

		if(state.loading){

			err.style.display = 'none';
			success.style.display = 'none';

			loading.style.display = "block";
		}
		else if(state.estates.add.added_new_estate){
			err.style.display = 'none';
			loading.style.display = 'none';

			success.style.display = "block";
			const estate_form = document.getElementById("add_estates_form")
			estate_form.reset()

		}
		else if (state.estates.add.attempted_added_new_estate) {
			success.style.display = 'none';
			loading.style.display = 'none';

			err.style.display = "block";
		}
		else {
			success.style.display = 'none';
			loading.style.display = 'none';
			err.style.display = "none";
		}
	}

	add_estate = (modelFunction) => {

		const estate_form = document.getElementById("add_estates_form")
		
		estate_form.onsubmit = (e) =>{

			e.preventDefault();

			const estate_title = document.getElementById("add_estates_form_title").value
			const estate_address = document.getElementById("add_estates_form_address").value
			const estate_google_maps = document.getElementById("add_estates_form_google_maps").value
			const estate_google_street = document.getElementById("add_estates_form_google_street").value
			const estate_text = document.getElementById("add_estates_form_text").value
			const estate_images = Array.from(
				document.getElementById("add_estates_form_images").files
			)

			const result = {}

			result.images = estate_images

			result.add_estate = {};

			result.add_estate.estate_title = estate_title;
			result.add_estate.estate_address = estate_address;
			if(estate_google_maps.length > 0){
				result.add_estate.estate_google_maps_url = estate_google_maps;
			}
			if(estate_google_street.length > 0){
				result.add_estate.estate_google_street_url = estate_google_street;
			}
			result.add_estate.estate_text = estate_text;
			result.add_estate.estate_id = estate_title;

			modelFunction(result)

		}

	}

	render_delete_estate = (state) => {

		const loading = document.getElementById("admin_estates_page_main_section_section1_article_div3_loading");
		const ul_titles = document.getElementById("admin_estates_page_main_section_section1_article_div3_ul1");

		const loading2 = document.getElementById("admin_estates_page_main_section_section1_article_div3_div3_loading");
		const success = document.getElementById("admin_estates_page_main_section_section1_article_div3_div3_success");
		const err = document.getElementById("admin_estates_page_main_section_section1_article_div3_div3_err");

		if(state.loading){

			err.style.display = 'none';
			success.style.display = 'none';

			loading.style.display = "block";
			loading2.style.display = "block";
		}
		else if(state.estates.delete.deleted_estate){
			err.style.display = 'none';
			loading.style.display = 'none';
			loading2.style.display = 'none';

			success.style.display = "block";

			ul_titles.replaceChildren();

			state.estates.view.estates_array.forEach((estate)=>{

	        	const li = document.createElement("li");

	        	li.classList.add("admin_estates_page_main_section_section1_article_div3_ul1_li")
	        	li.setAttribute("name", estate.estate_title)
	        	li.textContent = estate.estate_title
				
				ul_titles.appendChild(li)

				li.addEventListener("click", (event)=>{

					this.selected_delete_ul = event.currentTarget.getAttribute("name");

					const other_lis = Array.from(
						document.getElementsByClassName("admin_estates_page_main_section_section1_article_div3_ul1_li")
					)

					other_lis.forEach((other_li)=>{
						other_li.classList.remove("admin_estates_page_main_section_section1_article_div3_ul1_li_selected")
					})

					event.currentTarget.classList.add("admin_estates_page_main_section_section1_article_div3_ul1_li_selected")
						
				})
	        })

		}
		else if (state.estates.delete.attempted_deleted_estate) {
			success.style.display = 'none';
			loading.style.display = 'none';
			loading2.style.display = 'none';

			err.style.display = "block";

			ul_titles.replaceChildren();

			state.estates.view.estates_array.forEach((estate)=>{

	        	const li = document.createElement("li");

	        	li.classList.add("admin_estates_page_main_section_section1_article_div3_ul1_li")
	        	li.setAttribute("name", estate.estate_title)
	        	li.textContent = estate.estate_title
				
				ul_titles.appendChild(li)

				li.addEventListener("click", (event)=>{

					this.selected_delete_ul = event.currentTarget.getAttribute("name");

					const other_lis = Array.from(
						document.getElementsByClassName("admin_estates_page_main_section_section1_article_div3_ul1_li")
					)

					other_lis.forEach((other_li)=>{
						other_li.classList.remove("admin_estates_page_main_section_section1_article_div3_ul1_li_selected")
					})

					event.currentTarget.classList.add("admin_estates_page_main_section_section1_article_div3_ul1_li_selected")
						
				})
	        })
		}
		else if (state.estates.view.estates_array) {

			success.style.display = 'none';
			loading.style.display = 'none';
			loading2.style.display = 'none';
			err.style.display = 'none';

			ul_titles.replaceChildren();

			state.estates.view.estates_array.forEach((estate)=>{

	        	const li = document.createElement("li");

	        	li.classList.add("admin_estates_page_main_section_section1_article_div3_ul1_li")
	        	li.setAttribute("name", estate.estate_title)
	        	li.textContent = estate.estate_title
				
				ul_titles.appendChild(li)

				li.addEventListener("click", (event)=>{

					this.selected_delete_ul = event.currentTarget.getAttribute("name");

					const other_lis = Array.from(
						document.getElementsByClassName("admin_estates_page_main_section_section1_article_div3_ul1_li")
					)

					other_lis.forEach((other_li)=>{
						other_li.classList.remove("admin_estates_page_main_section_section1_article_div3_ul1_li_selected")
					})

					event.currentTarget.classList.add("admin_estates_page_main_section_section1_article_div3_ul1_li_selected")
						
				})
	        })
		}

	}

	delete_estate = (modelFunction)=>{

		const delete_btn = document.getElementById("admin_estates_page_main_section_section1_article_div3_div2_btn");

		delete_btn.addEventListener("click", (event)=>{

			modelFunction(this.selected_delete_ul)
		})
	}



}